/* Retro Personal Homepage Style - GeoCities Era */
.browser-viewport div#about-me {
  background:
    url('data:image/gif;base64,')
      repeat,
    linear-gradient(to bottom, #000033, #000066);
  color: #ffff00;
  font-family: 'Comic Sans MS', 'Times New Roman', serif;
  padding: 20px;
  min-height: 100%;
  position: relative;
}

/* Animated stars background */
.browser-viewport div#about-me::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/gif;base64,R0lGODlhBAAEAIAAAP///wAAACH5BAEAAAAALAAAAAAEAAQAAAIESAEJCgAh')
    repeat;
  opacity: 0.3;
  animation: twinkle 3s ease-in-out infinite;
  pointer-events: none;
}

@keyframes twinkle {
  0%,
  100% {
    opacity: 0.3;
  }
  50% {
    opacity: 0.6;
  }
}

/* Welcome Banner with Flames */
.browser-viewport div#about-me h1 {
  background: linear-gradient(180deg, #ff0000, #ff6600, #ffff00);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 36px;
  text-shadow: 2px 2px 4px rgba(255, 0, 0, 0.5);
  animation: flame 1.5s ease-in-out infinite;
  margin-bottom: 20px;
}

@keyframes flame {
  0%,
  100% {
    transform: scaleY(1);
  }
  50% {
    transform: scaleY(1.1);
  }
}

.browser-viewport div#about-me h2 {
  text-align: center;
  color: #00ff00;
  text-decoration: underline;
  font-family: 'Courier New', monospace;
  animation: glow 2s ease-in-out infinite;
}

@keyframes glow {
  0%,
  100% {
    text-shadow: 0 0 5px #00ff00;
  }
  50% {
    text-shadow:
      0 0 20px #00ff00,
      0 0 30px #00ff00;
  }
}

.browser-viewport div#about-me h3 {
  background: repeating-linear-gradient(90deg, #ff00ff, #ff00ff 10px, #00ffff 10px, #00ffff 20px);
  padding: 5px;
  text-align: center;
  color: #000000;
  font-weight: bold;
  border: 2px solid #ffffff;
  margin: 15px 0;
}

.browser-viewport div#about-me p {
  background: rgba(255, 255, 255, 0.1);
  border: 1px dotted #00ff00;
  padding: 10px;
  margin: 10px 0;
  color: #ffffff;
  font-size: 12px;
  line-height: 1.6;
}

/* Visitor Counter */
.browser-viewport div#about-me::after {
  content: 'You are visitor number: ' attr(data-counter) '31337';
  position: absolute;
  bottom: 10px;
  right: 10px;
  background: #000000;
  color: #00ff00;
  padding: 5px 10px;
  font-family: 'Courier New', monospace;
  font-size: 10px;
  border: 1px solid #00ff00;
}

/* Web Awards Section */
.web-awards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;
  margin: 20px 0;
  padding: 10px;
  border: 3px ridge #c0c0c0;
  background: rgba(192, 192, 192, 0.2);
}

.award-badge {
  width: 88px;
  height: 31px;
  background: linear-gradient(45deg, #ffd700, #ffed4e);
  border: 2px solid #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 8px;
  font-weight: bold;
  color: #000000;
  animation: sparkle 2s linear infinite;
}

@keyframes sparkle {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

/* MIDI Player Widget */
.midi-player {
  background: #c0c0c0;
  border: 2px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  padding: 5px;
  margin: 15px auto;
  width: 200px;
  text-align: center;
  font-family: 'MS Sans Serif', sans-serif;
  font-size: 11px;
  color: #000000;
}

.midi-player::before {
  content: 'j Now Playing: about_me.mid j';
  display: block;
  margin-bottom: 5px;
}

/* Button styling */
.browser-viewport div#about-me button {
  background: linear-gradient(to bottom, #ffffff, #c0c0c0);
  border: 2px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  color: #000000;
  font-family: 'MS Sans Serif', sans-serif;
  padding: 5px 15px;
  cursor: pointer;
  font-size: 11px;
}

.browser-viewport div#about-me button:active {
  border-color: #808080 #ffffff #ffffff #808080;
}

.browser-viewport div#about-me button:hover {
  background: linear-gradient(to bottom, #ffffcc, #c0c0c0);
}

/* Webring navigation */
.webring {
  text-align: center;
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #00ff00;
  background: rgba(0, 0, 0, 0.5);
}

.webring-text {
  color: #00ffff;
  font-size: 10px;
  font-family: 'Courier New', monospace;
}

/* Guestbook link */
.guestbook-link {
  display: inline-block;
  margin: 20px auto;
  text-align: center;
  animation: pulse 1s ease-in-out infinite;
}

@keyframes pulse {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

/* Email me GIF button */
.email-me-button {
  display: inline-block;
  width: 88px;
  height: 31px;
  background: linear-gradient(90deg, #ff0000, #ffff00, #00ff00, #00ffff, #0000ff, #ff00ff, #ff0000);
  background-size: 200% 100%;
  animation: rainbow-slide 2s linear infinite;
  border: 2px solid #000000;
  color: #ffffff;
  text-shadow: 1px 1px 2px #000000;
  font-weight: bold;
  font-size: 10px;
  line-height: 27px;
  text-align: center;
  cursor: pointer;
}

@keyframes rainbow-slide {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 200% 50%;
  }
}
